/* Estilos generales */
body {
    font-family: 'Roboto', sans-serif;
    background-color: #f5f5dc; /* Fondo crema */
    margin: 0;
  }
  
  /* Barra de menú */
  #navbar {
    position: fixed;
    top: 0;
    width: 100%;
    background-color: #333; /* Fondo gris oscuro */
    padding: 10px 30px;  
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 10;
    box-sizing: border-box;
  }
  
  #nav-logo {
    width: 50px; /* Ajusta el tamaño del logo */
  }
  
  #nav-title {
    color: white;
    margin-left: 10px;
    font-size: 24px;
    font-weight: 500;
  }
  
  /* Menú horizontal en pantallas grandes */
  #navbar ul {
    list-style: none;
    display: flex;
    gap: 15px;
    margin-left: auto;
  }
  
  #navbar ul li a {
    color: white;
    text-decoration: none;
    font-size: 16px;
  }
  
  #navbar ul li a:hover {
    color: #f5f5dc; /* Color claro al pasar el ratón */
  }
  
  /* Estilos del icono de hamburguesa */
  .hamburger-icon {
    display: none;
    font-size: 30px;
    color: white;
    cursor: pointer;
  }
  
  /* Menú en dispositivos pequeños */
  .menu {
    display: flex;
    gap: 15px;
  }
  
  #menu {
    display: flex;
    gap: 15px;
    margin-left: auto;
    padding-left: 20px;
  }
  
  /* Media query para pantallas más pequeñas (móviles y tablets) */
  @media (max-width: 768px) {
    #navbar ul {
      display: none; /* Ocultamos el menú */
      flex-direction: column;
      width: 100%;
      position: absolute;
      top: 60px; /* Ajusta la altura del menú */
      left: 0;
      background-color: #333;
    }
  
    #navbar ul li {
      width: 100%;
      text-align: center;
      padding: 10px 0;
    }
  
    /* Muestra el icono de hamburguesa en pantallas pequeñas */
    .hamburger-icon {
      display: block;
    }
  
    #navbar ul.active {
      display: flex; /* Muestra el menú cuando la clase 'active' esté presente */
    }
  }
  